import React, { useEffect } from "react";
// import { useNavigate } from "react-router-dom";
import cover_gif from "../../usables/pikachu_cover.gif";
import Modal from "@mui/material/Modal";
import "../../CSS/Cover.css";
import CameraModal from "../Modals/CameraModal";
import { useNavigate } from "react-router-dom";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

const Cover = () => {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const navigate = useNavigate();
  useEffect(() => {
    const auth = localStorage.getItem("user");
    if (!auth) {
      navigate("/login");
    }
  });
  const handleClose = async () => {
    try {
      const response = await fetch("/close-webcam", {
        method: "POST",
      });
      setOpen(false);
      if (response.ok) {
        console.log("Webcam closed");
      } else {
        console.error("Failed to close webcam");
      }
    } catch (error) {
      console.error("Error:", error);
    }
    window.location.reload();
  };

  return (
    <>
      {/* <h1>THIS IS SOMETHING</h1> */}
      <ul className="cover_box">
        <li>
          <img
            className="landing_cover"
            alt="this is a gif"
            src={cover_gif}
          ></img>
        </li>
        <li className="quote">
          <h1>Welcome to FACE-EX</h1>
          <p>
            "Experience the Future of Communication: Our Advanced Facial
            Expression Detection System Allows You to Not Only Decode Emotions
            but Also Enhance Your Ability to Connect, Understand, and Empathize
            with Others on a Profound Level."
          </p>
          <button className="joinusButton" onClick={handleOpen}>
            Start
          </button>
          <Modal
            open={open}
            onClose={handleClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
          >
            <CameraModal change={handleClose} />
          </Modal>
        </li>
      </ul>
    </>
  );
};

export default Cover;
