import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Menu from "@mui/material/Menu";
import MenuIcon from "@mui/icons-material/Menu";
import Container from "@mui/material/Container";
import Button from "@mui/material/Button";
import MenuItem from "@mui/material/MenuItem";
import EmojiEmotionsIcon from "@mui/icons-material/EmojiEmotions";
import { useNavigate } from "react-router-dom";

// const pages = [
//   { name: "Login", address: "/login" },
//   { name: "Sign Up", address: "/signup" },
// ];
const pages = ["login", "Sign Up"];

function Header() {
  const [anchorElNav, setAnchorElNav] = React.useState(null);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };
  const navigate = useNavigate();
  const handleCloseNavMenu = (page) => {
    console.log(page);
    if (page == "login") {
      navigate("/login");
    } else {
      navigate("signup");
    }
    // navigate(page.address);
    console.log("yooy");
    setAnchorElNav(null);
  };
  const logout = () => {
    navigate("/login");
    localStorage.removeItem("user");
    localStorage.removeItem("auth");
    // console.log("home ja na love day");
  };
  var auth = localStorage.getItem("user");
  if (auth) {
    auth = true;
  }
  console.log(auth);
  return (
    <AppBar position="static">
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <EmojiEmotionsIcon
            sx={{ display: { xs: "none", md: "flex" }, mr: 1 }}
          />
          <Typography
            variant="h6"
            noWrap
            component="a"
            href="#app-bar-with-responsive-menu"
            sx={{
              mr: 2,
              display: { xs: "none", md: "flex" },
              fontFamily: "monospace",
              fontWeight: 700,
              letterSpacing: ".3rem",
              color: "inherit",
              textDecoration: "none",
            }}
          >
            FACE-EX
          </Typography>

          <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "left",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: "block", md: "none" },
              }}
            >
              {!auth ? (
                pages.map((page) => (
                  <MenuItem key={page} onClick={() => handleCloseNavMenu(page)}>
                    <Typography textAlign="center">{page}</Typography>
                  </MenuItem>
                ))
              ) : (
                <MenuItem onClick={() => logout()}>
                  <Typography textAlign="center">Log Out</Typography>
                </MenuItem>
              )}
            </Menu>
          </Box>
          <EmojiEmotionsIcon
            sx={{ display: { xs: "flex", md: "none" }, mr: 1 }}
          />
          <Typography
            variant="h5"
            noWrap
            component="a"
            href="#app-bar-with-responsive-menu"
            sx={{
              mr: 2,
              display: { xs: "flex", md: "none" },
              flexGrow: 1,
              fontFamily: "monospace",
              fontWeight: 700,
              letterSpacing: ".3rem",
              color: "inherit",
              textDecoration: "none",
            }}
          >
            FACE-EX
          </Typography>
          <Box
            sx={{
              flexGrow: 1,
              display: {
                xs: "none",
                md: "flex",
              },
              justifyContent: "flex-end", // Align contents to the right
            }}
          >
            {!auth ? (
              pages.map((page) => (
                <Button
                  key={page}
                  onClick={() => handleCloseNavMenu(page)}
                  sx={{
                    my: 2,
                    color: "white",
                    display: "block",
                    fontSize: "1.1rem",
                    paddingInline: "1.5rem",
                  }}
                >
                  {page}
                </Button>
              ))
            ) : (
              <Button
                onClick={() => logout()}
                sx={{
                  my: 2,
                  color: "white",
                  display: "block",
                  fontSize: "1.1rem",
                  paddingInline: "1.5rem",
                }}
              >
                Log Out
              </Button>
            )}
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
export default Header;
