import React from "react";
import { Grid, Typography, Link, Container, Paper } from "@mui/material";
import "../../CSS/Footer.css";

const Footer = () => {
  return (
    <Paper elevation={3} style={{ backgroundColor: "#2196F3", height: "3rem" }}>
      <Container className="paper-box">
        <Grid
          container
          spacing={2}
          justifyContent="space-between"
          alignItems="center"
        >
          <Grid item>
            <Typography variant="body1">
              &copy; 2023 Your Company Name
            </Typography>
          </Grid>
          <Grid item>
            <Link
              className="Link"
              underline="none"
              color="inherit"
              href="/privacy-policy"
            >
              Privacy Policy
            </Link>
          </Grid>
          <Grid item>
            <Link
              className="Link"
              underline="none"
              color="inherit"
              href="/terms-of-service"
            >
              Terms of Service
            </Link>
          </Grid>
        </Grid>
      </Container>
    </Paper>
  );
};

export default Footer;
